
.main{
    display: block;
    position: relative;
    width: 100%;
    height: 300vh;
    background-color: rgb(233, 224, 243);

    margin: 0 auto;
    text-align: center;
    float: center;
}

/* 버튼이 위로 올라오게 위치잡기 */
.div_1{
    position: relative;
    z-index: 1;
}
.div_2{
    position: absolute;
    z-index: 2;
}