.update{
    display: block;
    max-width: 100vw;
    height: 100vh;
    background-color: rgb(233, 224, 243);

    margin: 0 auto;
    text-align: center;
}

.update_box>hr{
    border: 0.5px solid rgb(126, 84, 152);
    margin-bottom: 30px;
}

.update_box{
    display: inline-block;
    margin-top: 110px;
    width: 500px;
    height: 470px;
    border: solid 2px rgb(126, 84, 152);
    border-radius: 20px;
}

.title{
    font-weight: bold;
    color: rgb(126, 84, 152);
    font-size: 25px;
}

.update_box>input{
    color: rgb(104, 37, 146);
    width: 300px;
    height: 20px;
    border: none;
    background-color: rgb(233, 224, 243);
    border-bottom: 1.5px solid rgb(126, 84, 152);

    margin-bottom: 20px;
}

.btn_updatd>button{
    margin-top: 12px;

    width: 100px;
    height: 30px;
    padding: 4px;
    box-sizing: border-box;

    border: none;
    border-radius: 20px;
    background-color: rgb(175, 109, 216);
    color: white;
    font-weight: bold;
    font-size: 18px;
}