
.word_card{
    display: block;
    padding-top: 5%;

    width: 90%;
    height: initial;
    /* background-color: aqua; */

    margin: 0 auto;

    text-align: left;

}
.card{
    display: inline-block;
    width: 30%;
    min-height: 180px;
    border: solid 2px rgb(126, 84, 152);;
    margin: 10px;
    border-radius: 15px;

    text-align: left;
    padding-left: 20px;
}
.card>h1{
    color: rgb(157, 0, 255);
    font-size: 35px;
    margin: 7px 0px 0px 0px;
    padding-top: 10px;
    padding-bottom: 15px;
}
/* 나머지 <p> 공통 */
.card>p{
    color: rgb(136, 90, 165);
    font-size: 20px;
    margin: 5px 0px 0px 0px;
    padding-bottom: 5px;
}

.btns{
    float: right;
    margin-top: 13px;
    margin-right: 13px;
}